body {
    font-family: 'Baloo Da 2', cursive;
}
div{
    outline: none !important;
}
.Slider-Wrapper{
    height: 100%;
    width: 55%;
    margin: 0 auto;
    margin-top: 2%;
   
}
.Slider-Block-Wrapper{
    display: flex;
    
  
}
.Slider-Icon{
    width: 5%;
    height: 5%;
}
.Slider-Font-Big {
    font-family: 'Baloo Da 2', cursive;
    font-size: 2.8vh;
    font-weight: 800;
    color: #52586A;
}
.Slider-Font-Small{
    font-family: 'Baloo Da 2', cursive;
    font-size: 2vh;
    font-weight: 600;
    color: #52586A;
    opacity: .6;
}
.dot{
    font-family: 'Baloo Da 2', cursive;
    font-size: 2.8vh;
    font-weight: 800;
    color: #17bed2;
    
}
.Parent{
    display: flex;
    justify-content: center;
    margin: 0 auto;
}