*{
    font-family: 'Baloo Da 2', cursive;
}
.FooterNav-Wrapper{
    height: 20vh;
    width: 63.5%;
    margin: 0 auto;
    /* border: 1px solid red; */
    display: flex;
    align-items: center;
}
.footer-nav{
    /* border: 1px solid black; */
    text-decoration: none;
    list-style: none;
    display: flex;
    flex-direction: column;
    
    
    justify-content: space-around;
   height: 80%;
    width: 20%;
    
}
.footer-nav>li>a{
    color: #333;
    font-weight: 550;
    text-decoration: none;
    list-style: none;
    
}
a.NavBarLinks:hover{
    color: #333;
}
a.NavBarLinks:hover::after {
    content: "";
    background-color: #17bed2;
    font-size: 90%;
    padding: 2px;
    display: inline-block;
    width: 10px;
    height: 0.3px;
    border-radius: 25%;
    margin: 2px;
    text-decoration: none;
    list-style: none;
}
.links-nav>li>a{
    color: #999CAA;
    font-weight: 550;
    text-decoration: none;
    list-style: none;
    
}@media (max-width: 768px){
    .FooterNav-Wrapper{
        height: auto;
        flex-direction: column;
        margin-top: 5%;
    }
    .footer-nav{
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
    }
}