* {
    font-family: 'Baloo Da 2', cursive;

}

.countries-container {
    padding-top: 2vh;
    /* border: 1px solid black; */
    width: 65%;
    height: 30vh;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;

}

.Country {
    /* border: 1px solid red; */
    width: 33%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.Country-first-block {
    /* border: 1px solid green; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Country-second-block {
    font-size: 1.7vh;
    font-weight: 550;
    color: #888a8f;
    padding-top: 2vh;
}

.Country-name {
    font-size: 3vh;
    font-weight: 800;
    color: #52586A;
    font-family: 'Baloo Da 2', cursive;
}

.Country-type {
    font-size: 1.5vh;
    font-weight: 1000;
    color: #888a8f;
    font-family: 'Baloo Da 2', cursive;
}

.phone-icon {
    width: 5%;
}

@media (max-width: 768px) {
    .countries-container {
        width: auto;
        height: auto;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;

    }

    .Country {
        /* border: 1px solid red; */
        width: 80%;
        display: flex;
        
    }
}
