.About_wrapper {
    width: 65%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* border: 1px solid red; */

}

.About_text {
    width: 70%;
    /* border: 1px solid red; */

}

.about_big_text {
    font-size: 4vh;
    color: #515769;
}

.about_small_text {
    font-size: 1.7vh;
    font-weight: 550;
    color: #888a8f;
    padding-top: 2vh;
}

.About_picture {
    width: 70%;
    height: 70%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    /* border: 1px solid red; */

}

.About_img {
    /* position: relative; */
    width: inherit;
    display: flex;
    
    
}
.About_Clients{
    margin-top: 5vh;
}

@media (max-width: 768px) {
    .About_wrapper {
        flex-direction: column;
    }

    .About_text {
        width: 100%;
    }

    .About_picture {
        width: 95%;
        justify-content: center;
    }

    .About_img {
        margin-top: 2vh;
    }
}
.border-bottom-benefits{
    border-bottom: none;
}