body {
    font-family: 'Baloo Da 2', cursive;
}
.benefits_container {
    width: 65%;
    height: 30vh;
    margin: 0 auto;
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.border-bottom-benefits{
    border-bottom: 1px solid #d3d1d1;
}

.benefits_container>div {
    /* border: 1px solid red; */
    width: 25vw;
    margin: 2vh;
}

.Head-name {
    font-size: 3vh;
    font-weight: 800;
    color: #52586A;
    font-family: 'Baloo Da 2', cursive;
}

.Article {
    font-size: 1.5vh;
    font-weight: 300;
    color: #888a8f;
    padding-top: 2vh;
    font-family: 'Baloo Da 2', cursive;

}

.Link-benefits>a {
    text-decoration: none;
    list-style: none;
    font-size: 1.6vh;
    color: #717275;
    font-weight: 800;
}
a.links:hover{
    text-decoration: none;
    list-style: none;
    color: #333;;
}

a.links:after {
    content: "";
    color: #333;
    background-color: #17bed2;;
    font-size: 90%;
    padding: 2px;
    display: inline-block;
    width: 10px;
    height: 1px;
    border-radius: 25%;
    margin: 2px;
}@media (max-width: 768px) {
    .benefits_container{
        height: auto;
        flex-direction: column;
    }.benefits_container>div{
        width: 100%;
    }
  }
  .border_bottom_white_benefits{
      border-bottom: 1px solid rgb(230, 223, 223);
  }