body {
    font-family: 'Baloo Da 2', cursive;
}
.work-container {
    /* height: 40vh; */
    /* border: 1px solid black; */
    display: flex;
    flex-wrap: wrap;
    width: 65%;
    margin: 0 auto;
    margin-top: 5vh;
}
.work-text {
    position: relative;
  margin-left: 1vw;
    /* border: 1px solid gold; */
    width: 45%;
    height: 30%;
}

.work-big-font {
    font-size: 4vh;
    color: #515769;
}

.work-small-font {
    font-size: 1.7vh;
    font-weight: 550;
    color: #888a8f;
    padding-top: 2vh;
}
@media (max-width: 768px){
    .work-text{
        width: 100%;
    }
}