.FormApp {
  /* border: 1px solid black; */
  width: 64%;
  margin: 0 auto;
}

.form-container {
  /* border: 1px solid rgb(173, 167, 167); */
  width: 45%;
  height: auto;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-container > div > input,
.form-container > div > textarea {
  border: 1px solid rgb(240, 236, 236);
  margin: 1vh;
  padding-left: 1vh;
}

.form-container > div {
  width: 100%;

  /* border: 1px solid red */
}

.submit-button {
  width: 15vh;
  height: 5vh;
  margin: 0 auto;
  background: #17bed2;
  border-radius: 99em;
  box-shadow: -1px -1px 47px 0px rgba(186, 182, 186, 1);
  outline: none;
}
.submit-button:hover{
    opacity: 0.9;
    transform: translateY(-0.1vh);
}

.form-container > div > p {
  /* border: 1px solid red; */
  color: #17bed2;
  font-weight: 900;
}

@media (max-width: 1000px) {
  .FormApp {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .form-container {
    width: 90%;
  }

  .form-container > div {
    width: 80%;
  }
}
